@import 'fonts';

.home-wrapper {
  .home-slider {
    display: flex;
    justify-content: center;
    padding: 20px 5%;



    // .container {
    //   img {
    //     width: 250px;
    //     height: 280px;
    //   }
    // }

//////////////////////////////////////////////////////

      .slider {
          width: 800px;
          height: 400px;
          overflow: hidden;
          position: relative;
          background: #e6e6e6;
          border: 20px solid #FFF;
          margin-top: 20px;
      }

      .slide {
          position: absolute;
          top: 0;
          left: 0;
      }

      .arrow {
          position: absolute;
          z-index: 1002;
          display: block;
          top: 50%;
          margin-top: -35px;
          width: 36px;
          height: 71px;
          outline: none;
          cursor: pointer;
          background: url(../assets/img/blank.gif) no-repeat;
      }

      .arrow.prev {
          opacity: 0.2;
          left: 20px;
          background-image: url(../assets/img/paddle_prev_999.png);
          transition: 0.2s linear all;
      }

      .arrow.next {
          opacity: 0.2;
          right: 20px;
          background-image: url(../assets/img/paddle_next_999.png);
          transition: 0.2s linear all;
      }

      .arrow.prev:hover {
          opacity: 1;
          background-image: url(../assets/img/paddle_prev_fff.png);
      }

      .arrow.next:hover {
          opacity: 1;
          background-image: url(../assets/img/paddle_next_fff.png);
      }



      /*---------------------------------------------------------
      NAV
      ---------------------------------------------------------*/

      .nav {
          text-align: center;
          display: block;
          position: absolute;
          z-index: 1002;
          left: 0;
          bottom: -4px;
          right: 0;
          height: 48px;
      }

      .nonDraggableImage{
          -webkit-user-drag: none;
      }

      .nav .wrapper {
          margin: 0 auto;
          width: 100%;
          padding: 1em 0 .8em;
      }

      .nav ul {
          margin: 0;
          width: 100%;
      }

      .nav .dot, .nav .dot a {
          display: inline-block;
          zoom: 1;
      }

      .dots .dot {
          position: relative;
          margin: 0 8px;
          width: 12px;
          height: 12px;
      }

      .dots .dot a {
          position: absolute;
          top: 2px;
          left: 2px;
          width: 6px;
          height: 6px;
          text-indent: 100%;
          white-space: nowrap;
          overflow: hidden;
          background: #FFF;
          border: 1px solid transparent;
          outline: none;
          -webkit-box-shadow: none;
          -moz-box-shadow: none;
          box-shadow: none;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          border-radius: 50%;
          -webkit-transition: background-color 0.25s, border-color 0.25s;
          -moz-transition: background-color 0.25s, border-color 0.25s;
          transition: background-color 0.25s, border-color 0.25s;
      }

      .dots .dot a.active {
          border-color: #FFF;
          background-color: transparent;
      }
/////////////////////////////////////////////////////

    // .home-slide {
    //   height: 250px;
    //   width: 95%;
    //   background: green;
    //   border-radius: 5%;
    // }





  }

  .home-text {
    padding: 10px 5% 25px 5%;
    .home-text1 {
      margin-bottom: 20px;
    }
    .home-heading {
      color: #3f3f3f;
      font-family: $av;
      font-size: 26px;
      text-decoration: underline;
      line-height: 36px;
    }
    p {
      font-family: $robreg;
      color: #545454;
      line-height: 25px;
      text-align: justify;
    }

    .home-text2 {
      clear: right;
      margin-bottom: 20px;
      img {
        float: left;
        height: 120px;
        margin-right: 14px;
        border-radius: 50%;
        border: 3px solid black;
      }
    }

    .home-text3 {
      a {
        text-decoration: none;
      }
      a:hover {
        color: #608541;
        cursor: pointer;
      }
    }
  }
}
